header {
  background-color: rgba(56,84,99,0.7);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 60px;
}

.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 100%;
  -ms-flex-align: center;
      align-items: center;
  max-width: 1000px;
  margin: auto;
  padding: 0 60px;
}

.header h1, a {
  margin: 0;
  padding: 0;
}

.header a {
  color: white;
  text-decoration: none;
  outline: none;
}

.header a:hover {
  border-bottom: 2px solid rgb(0, 255, 200);
}

.header a.is-active {
  border-bottom: 2px solid white;
}

.header__right a {
  margin-left: 20px;
  font-size: 1.2em;
}


