.opponentContainer{
    margin-left: 30px;
    margin-right:30px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    
}

.opponentDescription{
    display: -ms-flexbox;
    display: flex;
    width: 150px;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    
    background: rgb(192, 191, 191);
    font-size: 15px;
    height: 50%;
    padding: 0;
    word-break: break-all;
    word-wrap: break-word;
}

.playersbutton{
    width: 90%;
    height: 30px;
    font-size: 14px;
    font-weight: bold;
    margin:0 auto;
    background:rgb(204, 153, 238);
    text-transform: uppercase;
}

.Invitation{
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    height: 100%;
    background: rgb(89, 141, 201);
    font-size:15 px;
}

.button-accept-invitation{
    width:80%;
    color: rgb(27, 133, 23);
    font-weight: 900;
    font-size: 18px;
}

.button-decline-invitation{
    width:80%;
    color: rgb(226, 4, 4);
    font-weight: 900;
    font-size: 18px;
}
.Invite{
    color: darkred;
    font-weight: 900;
    font-size: 18px;
}
.Timer{
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    height: 100%;
    background: rgb(245, 243, 161);
}
.countdown{
    color: darkred;
    font-weight: 900;
    font-size: 14px;
    text-transform: uppercase;
}
.GamePlay{
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    height: 100%;
}
.linescleared{
    color: rgb(56, 29, 212);
    font-weight: 900;
    font-size: 18px;
}
.gamesplayed{
    color: rgb(32, 122, 62);
    font-weight: 900;
    font-size: 18px;
}
.endGame{
    background: rgb(247, 214, 136);
    height: 100%;
    font-size:18 px;
    font-weight: 900;
}

.winner{
    color: rgb(11, 165, 50);
}

.looser{
    color: rgb(212, 10, 10);
}

.loading{
    margin:0 auto;
    width:50px;
    height: 50px;
    border: 20px solid black;
    border-top-color: #1a6aed;
    border-bottom-color: #f49b42;
    border-radius: 50%;
    -webkit-animation-name: picloading;
            animation-name: picloading;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes picloading {
    0%{
      -webkit-transform: rotate(0deg) scale(1);
              transform: rotate(0deg) scale(1);
    }
    50%{
      -webkit-transform: rotate(360deg) scale(1.05);
              transform: rotate(360deg) scale(1.05);
    }
    100%{
      -webkit-transform: rotate(720deg) scale(1);
              transform: rotate(720deg) scale(1);
    }
  }

@keyframes picloading {
    0%{
      -webkit-transform: rotate(0deg) scale(1);
              transform: rotate(0deg) scale(1);
    }
    50%{
      -webkit-transform: rotate(360deg) scale(1.05);
              transform: rotate(360deg) scale(1.05);
    }
    100%{
      -webkit-transform: rotate(720deg) scale(1);
              transform: rotate(720deg) scale(1);
    }
  }
  


