.controls{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    width:auto;
    background: grey;
    margin-right: 5%;
    color: rgb(128, 224, 248);
