button{
  border-radius: 10px;
  margin-top:10px;
  height:50px;
  font-size:25px;
  width:80%;
  cursor:pointer;
}

.buttonmulti-multi{
  background: rgb(141, 247, 141);
}

.buttonmulti-single{
  background: rgb(121, 138, 184);
}


